<template>
  <div
    class="field"
  >
    <p class="control has-icons-left">
      <span class="icon is-left">
        <i class="fas fa-envelope" />
      </span>
      <Input
        :id="qid"
        v-model="answer"
        type="email"
        class="input"
        :placeholder="question.properties.placeholder"
        :required="question.required"
        @input="update"
      />
    </p>
  </div>
</template>

<script>
  import Input from "@/components/Input";
  export default {
    name: "EmailField",
    props: ["question", "questionIndex", "sectionIndex", "qid", "value"],
    emits: ["update"],
    components: {Input},
    data() {
      return {
        answer:
          this.value !== undefined
            ? this.value
            : this.question.properties.default
      };
    },
    mounted() {
      this.update();
    },
    methods: {
      update() {
        this.$emit("update", {
          sectionIndex: this.sectionIndex,
          questionIndex: this.questionIndex,
          answer:  this.answer,
        });
      }
    }
  };
</script>
