<template>
  <div
    class="flex row"
  >
    <span
      v-if="question.properties.style == 'slider'"
      class="flex row vca"
      style="margin-right: 10px"
    >
      {{ question.properties.prefix }}
      <input
        :id="qid"
        v-model="answer"
        type="range"
        class="spaced ml-1 mr-1"
        :min="question.properties.min"
        :max="question.properties.max"
        :step="question.properties.decimal_places !== undefined ? Math.pow(10, -question.properties.decimal_places) : (question.properties.max-question.properties.min)/100.0"
        :required="question.required"
        @change="update"
        @invalid="invalid = 'invalid'"
      >
      {{ question.properties.suffix }}
    </span>
    <span class="flex row field has-addons">
      <p
        v-if="question.properties.prefix && question.properties.style != 'slider'"
        class="control"
      >
        <a class="button is-static">
          {{ question.properties.prefix }}
        </a>
      </p>
      <p class="control">
        <Input
          :id="question.properties.style == 'slider' ? qid + '_alt' : qid"
          v-model="answer"
          type="number"
          class="input"
          :class="{'is-small' : question.properties.style == 'slider'}"
          :min="question.properties.min"
          :max="question.properties.max"
          :step="question.properties.decimal_places !== undefined ? Math.pow(10, -question.properties.decimal_places) : (question.properties.max-question.properties.min)/100.0"
          :required="question.required"
          @input="update"
          @invalid="invalid = 'invalid'"
        />
      </p>
      <p
        v-if="question.properties.suffix && question.properties.style != 'slider'"
        class="control"
      >
        <a class="button is-static">
          {{ question.properties.suffix }}
        </a>
      </p>
    </span>
  </div>
</template>

<script>
import Input from "@/components/Input";
export default {
  name: "NumberField",
  props: ["question", "questionIndex", "sectionIndex", "qid", "value"],
  emits: ["update"],
  components: {Input},
  data() {
    return {
      answer:
        this.value !== undefined
          ? this.value
          : this.question.properties.default,
      invalid: false,
    };
  },
  mounted() {
    this.update();
  },
  methods: {
    update() {
      this.$emit("update", {
        sectionIndex: this.sectionIndex,
        questionIndex: this.questionIndex,
        answer: this.answer !== undefined ? Number(this.answer) : this.answer,
      });
    },
  },
};
</script>
