<template>
  <div
    class="field has-addons"
  >
    <p class="control">
      <a class="button is-static">
        +351
      </a>
    </p>
    <p class="control">
      <Input
        :id="qid"
        v-model="answer"
        type="tel"
        class="input"
        pattern="(?:9[1-36][0-9]|2[12][0-9]|2[35][1-689]|24[1-59]|26[1-35689]|27[1-9]|28[1-69]|29[1256]|3[0-9]{2})[0-9]{6}"
        minlength="9"
        maxlength="9"
        :placeholder="question.properties.placeholder"
        :required="question.required"
        @input="update"
      />
    </p>
  </div>
</template>

<script>
import Input from "@/components/Input";
export default {
  name: "NumberField",
  props: ["question", "questionIndex", "sectionIndex", "qid", "value"],
  emits: ["update"],
  components: {Input},
  data() {
    return {
      answer:
        this.value !== undefined
          ? this.value
          : this.question.properties.default
    };
  },
  mounted() {
    this.update();
  },
  methods: {
    update() {
      this.$emit("update", {
        sectionIndex: this.sectionIndex,
        questionIndex: this.questionIndex,
        answer: this.answer !== undefined ? Number(this.answer) : this.answer,
      });
    }
  }
};
</script>

<style scoped>
  input::-webkit-outer-spin-button,
  input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }

  /* Firefox */
  input[type=number] {
    -moz-appearance: textfield;
  }
</style>