<template>
  <div
    class="flex row width-100"
  >
    <!-- <span
      v-if="question.properties.max_length"
      class="flex row width-100 max-char"
      style="position: absolute;"
    >Caracteres: {{ answer ? answer.length : 0 }}/{{
      question.properties.max_length
    }}</span> -->
    <textarea
      :id="qid"
      v-model="answer"
      class="textarea"
      :class="{'is-warning' : !invalid && validation_invalid && answer, 
               'is-danger' : invalid && validation_invalid
      }"
      :required="question.required"
      :rows="question.properties.default_lines"
      :minlength="question.properties.min_length"
      :maxlength="question.properties.max_length"
      :placeholder="question.properties.placeholder"
      @input="update"
      @invalid="invalid = true"
    />
  </div>
</template>

<script>
export default {
  name: "LongTextField",
  props: ["question", "questionIndex", "sectionIndex", "qid", "value"],
  emits: ["update"],
  data() {
    return {
      answer:
        this.value !== undefined
          ? this.value
          : this.question.properties.default,
      invalid: false,
    };
  },
  mounted() {
    this.update();
  },
  methods: {
    update() {
      this.$emit("update", {
        sectionIndex: this.sectionIndex,
        questionIndex: this.questionIndex,
        answer: this.answer,
      });
    },
  },
  computed : {
    validation_invalid() {
      if (!this.answer)
        return this.question.required;
      return this.answer.length < this.question.properties.min_length ||
        this.answer.length > this.question.properties.max_length;
    }
  }
};
</script>
