<template>
  <div
    v-if="!valid"
    class="icon-text mb-2 flex row vca ca"
    role="alert"
    aria-readonly="true"
  >
    <span
      class="icon is-medium mt-1"
      :class="{'has-text-danger' : invalid}"
    >
      <i class="fas fa-exclamation-triangle" />
    </span>
    <span style="max-width: 70vw; overflow: auto;">
      <input
        :key="valid"
        v-autowidth="{comfortZone: 0}"
        :value="invalidString"
        class="input is-static is-unselectable invalidator"
        :class="{'has-text-danger' : invalid}"
        type="text"
        oninvalid="setCustomValidity('Confirme a informação introduzida.')"
        :pattern="valid ? undefined : '.a'"
        :title="invalidString"
        onkeydown="return false;"
        draggable="false"
        @invalid="$emit('invalid'); invalid = true"
      >
    </span>
  </div>
</template>

<script>
  import VueInputAutowidth from 'vue-input-autowidth';
  export default {
    name: "FormInvalidator",
    directives: {
      autowidth: {
        beforeMount: VueInputAutowidth.bind,
        mounted: VueInputAutowidth.inserted,
        updated: VueInputAutowidth.componentUpdated,
        unmounted: VueInputAutowidth.unbind,
      },
    },
    data() {
      return {
        invalid : this.startInvalid === undefined
      }
    },
    props : ["invalidString", "valid", "startInvalid"],
    emits : ["invalid"],
  };
</script>