<template>
  <div>
    <div
      v-for="option in question.properties.options"
      :key="option.id"
      class="flex row vca"
    >
      <label
        class="checkbox"
        :for="qid + '_o' + option.id"
      >
        <input
          :id="qid + '_o' + option.id"
          v-model="answer"
          class="mr-1"
          type="checkbox"
          :value="option.id"
          @change="update"
        >
        {{ option.question }}</label>
    </div>
    <div v-if="question.properties.allowed_other">
      <div
        v-for="(other, index) in others_selected"
        :id="qid + '_other' + index + '_parent'"
        :key="index"
        class="removable"
      >
        <div>
          <label
            class="checkbox flex row vca"
            style="width: fit-content; margin-bottom: 1px;"
            :for="answer == 'other' ? qid + '_otherString' + index : qid + '_other' + index"
          >
            <input
              :id="qid + '_other' + index"
              v-model="others_selected[index]"
              type="checkbox"
              class="mr-2"
              :name="qid + '_other' + index"
              :value="others[index]"
              @change="add_new(index)"
            >
            {{
              question.properties.other_string
            }}
            <Input
              v-if="question.properties.max_length != 0 && others_selected[index]"
              :id="qid + '_otherString' + index"
              v-model="others[index]"
              type="text"
              class="input is-small ml-1 pt-0 pb-0"
              style="cursor: text; height: 25px;"
              :placeholder="question.properties.placeholder"
              :minlength="question.properties.min_length"
              :maxlength="question.properties.max_length"
              :required="question.properties.min_length"
              @input="update"
            />
          </label>
        </div>
      </div>
    </div>
    <div
      class="flex row la"
      style="margin-bottom: 40px"
    >
      <FormInvalidator 
        :valid="valid === undefined || valid === ' '"
        :invalid-string="valid"
        :start-invalid="triedSubmit"
        style="position: absolute;"
        :class="{'hidden' : !triedSubmit && valid == translation.mandatoryField}"
        @invalid="$emit('uncollapse'); triedSubmit = true;"
      />
    </div>
  </div>
</template>

<script>
import FormInvalidator from "@/components/FormInvalidator";
import Input from "@/components/Input";
export default {
  name: "CheckBoxField",
  props: ["question", "questionIndex", "sectionIndex", "qid", "value"],
  components : {FormInvalidator, Input},
  emits: ["update", "uncollapse"],
  data() {
    return {
      others: [],
      others_selected: [false],
      full: false,
      mounted: false,
      valid: " ",
      invalid: "",
      answer: this.value,
      translation: {
        at_least: "Deve selecionar pelo menos ",
        at_most: "Deve selecionar no máximo ",
        all : "Deve selecionar todas as opções",
        options: " opções.",
        mandatoryField: "Este é um campo obrigatório.",
      },
      triedSubmit : false
    };
  },
  mounted() {
    if (this.value !== undefined) {
      var j = 0;
      this.answer.reverse();
      for (var i = this.answer.length - 1; i >= 0; i--) {
        if (typeof this.answer[i] != "number") {
          this.others_selected[j] = true;
          this.add_new(j);
          this.others[j++] = this.answer[i];
          this.answer.pop();
        }
      }
    }
    else {
      this.answer = [];
      for (var index = 0; index < this.question.properties.options.length; index++) {
        if (this.question.properties.options[index].default)
          this.answer.push(this.question.properties.options[index].id);
      }
    }
    this.valid = this.validate(this.answer.concat(this.others));
  },
  methods: {
    validate(answer) {
      let selected_normal = 0;
      let selected_other = 0;
      for (var i = 0; i < answer.length; i++) {
        if (answer[i] === parseInt(answer[i], 10)) selected_normal++;
        else selected_other++;
      }
      let selected_total = selected_normal + selected_other;
      if (answer.length == 0) {
        if (this.question.required) return this.translation.mandatoryField;
      } else {
        if (typeof this.question.properties.min_selected !== "undefined")
          if (selected_total < this.question.properties.min_selected) {
            if (this.question.properties.min_selected === this.question.properties.options.length)
              return this.translation.all;
            return (
              this.translation.at_least +
              this.question.properties.min_selected +
              this.translation.options
            );
          }

        if (typeof this.question.properties.max_selected !== "undefined")
          if (selected_total > this.question.properties.max_selected)
            return (
              this.translation.at_most +
              this.question.properties.max_selected +
              this.translation.options
            );
      }
      return " ";
    },
    update() {
      this.$nextTick(function () {
        let answer = this.answer.concat(this.others);
        this.valid = this.validate(answer);
        this.$emit("update", {
          sectionIndex: this.sectionIndex,
          questionIndex: this.questionIndex,
          answer: answer.length == 0 ? undefined : answer,
          valid: this.valid,
        });
      });
    },
    async add_new(index) {
      // typeof this.others[index+1] === 'undefined'
      if (this.others_selected[index] == true) {
        this.others[index] = "";
        if (
          typeof this.question.properties.max_other === "undefined" ||
          index + 1 < this.question.properties.max_other
        ) {
          this.others_selected.push(false);
          const next = index + 1;
          this.$nextTick(async function () {
            document.getElementById(
              this.qid + "_other" + next + "_parent"
            ).style.opacity = 0;
            await new Promise((r) => setTimeout(r, 1));
            document.getElementById(
              this.qid + "_other" + next + "_parent"
            ).style.opacity = 1;
          });
        } else this.full = true;
      } else if (this.others_selected.length > 1) {
        document.getElementById(
          this.qid + "_other" + index + "_parent"
        ).style.opacity = 0;
        await new Promise((r) => setTimeout(r, 100));
        this.others.splice(index, 1);
        this.others_selected.splice(index, 1);
        if (this.full) {
          this.others_selected.push(false);
          this.full = false;
        }
        document.getElementById(
          this.qid + "_other" + index + "_parent"
        ).style.opacity = 1;
      }

      this.update();
    },
  },
};
</script>