<template>
  <div>
    <div v-if="question.properties.style == 'radio'">
      <div
        v-for="option in question.properties.options"
        :key="option.id"
        class="control"
      >
        <label
          class="radio"
          :for="qid + '_o' + option.id"
        >
          <input
            :id="qid + '_o' + option.id"
            v-model="answer"
            class="mr-1"
            type="radio"
            :name="qid+ '_o' + option.id"
            :value="option.id"
            @change="update"
          >
          {{ option.question }}
        </label>
      </div>
      <div
        v-if="question.properties.allowed_other"
      >
        <label
          class="radio flex row vca"
          style="width: fit-content"
          :for="answer == 'other' ? qid + '_otherString' : qid + '_other'"
        >
          <input
            :id="qid + '_other'"
            v-model="answer"
            type="radio"
            class="mr-2"
            :name="qid"
            value="other"
            @change="update"
          >
          {{ question.properties.other_string }}
          <Input
            v-if="
              question.properties.max_length != 0 &&
                answer &&
                answer != parseInt(answer)
            "
            :id="qid + '_otherString'"
            v-model="other_value"
            type="text"
            class="input is-small ml-1"
            style="cursor: text; height: 25px;"
            :placeholder="question.properties.placeholder"
            :minlength="question.properties.min_length"
            :maxlength="question.properties.max_length"
            :required="question.properties.min_length"
            @input="update"
            @invalid="invalid = 'invalid'"
          />
        </label>
      </div>
      <div class="m-2" />
      <a
        v-if="!question.required"
        style="text-decoration:underline"
        @click="clearSelection"
      >
        Limpar Seleção
      </a>
      <div
        class="flex row la"
      >
        <FormInvalidator 
          :valid="valid === undefined || valid === ' '"
          :invalid-string="valid"
          :start-invalid="triedSubmit"
          :class="{'hidden' : !triedSubmit}"
          @invalid="$emit('uncollapse'); triedSubmit = true;"
        />
      </div>
    </div>
    <div v-else>
      <select
        :id="qid"
        v-model="answer"
        class="select"
        :required="question.required"
        @invalid="invalid = 'invalid'"
        @change="update"
      >
        <option
          v-if="!question.required"
          value=""
        />
        <option
          v-for="option in question.properties.options"
          :id="qid + '_o' + option.id"
          :key="option.id"
          :value="option.id"
        >
          {{ option.question }}
        </option>
      </select>
    </div>
  </div>
</template>

<script>

import FormInvalidator from "@/components/FormInvalidator"
import Input from "@/components/Input";
export default {
  name: "MultipleChoiceField",
  props: ["question", "questionIndex", "sectionIndex", "qid", "value"],
  emits: ["update", "uncollapse"],
  components: {FormInvalidator, Input},
  data() {
    return {
      answer:
        this.value !== undefined
          ? this.value != parseInt(this.value, 10)
            ? "other"
            : this.value
          : this.question.properties.default,
      other_value: this.value != parseInt(this.value, 10) ? this.value : undefined,
      valid: "",
      translation: {
        required: "Isto é um campo obrigatório",
      },
      invalid: "",
      triedSubmit : false
    };
  },
  mounted() {
    this.update();
  },
  methods: {
    clearSelection() {
      this.answer = undefined;
      this.update();
    },
    update() {
      if (this.answer === "")
        this.answer = undefined;
      if (typeof this.answer === "undefined" && this.question.required) {
        this.valid = this.translation.required;
      } else {
        this.valid = " ";
      }
      this.$emit("update", {
        sectionIndex: this.sectionIndex,
        questionIndex: this.questionIndex,
        answer: this.answer == "other" ? this.other_value : this.answer,
      });
    },
  },
};
</script>