<template>
  <div>
    <Input
      :id="qid"
      v-model="answer"
      type="number"
      class="input"
      :class="{'is-warning' : !triedSubmit && !valid, 'is-danger' : triedSubmit && !valid}"
      :placeholder="question.properties.placeholder"
      :required="question.required"
      @input="update()"
    />
    <FormInvalidator 
      :valid="valid"
      invalid-string="Deve introduzir um NIF válido."
      :class="{'hidden' : !triedSubmit}"
      :start-invalid="triedSubmit"
      @invalid="$emit('uncollapse'); triedSubmit = true;"
    />
  </div>
</template>

<script>
import FormInvalidator from "@/components/FormInvalidator";
import Input from "@/components/Input";
export default {
  name: "NumberField",
  props: ["question", "questionIndex", "sectionIndex", "qid", "value"],
  emits: ["update"],
  components: {Input, FormInvalidator},
  data() {
    return {
      answer:
        this.value !== undefined
          ? this.value
          : this.question.properties.default,
      triedSubmit: false
    };
  },
  mounted() {
    this.update();
  },
  methods: {
    update() {
      this.$emit("update", {
        sectionIndex: this.sectionIndex,
        questionIndex: this.questionIndex,
        answer: this.answer !== undefined ? Number(this.answer) : this.answer,
      });
    },
    validateNIF(nif) {// https://gist.github.com/Pipas/69ab5b514a53a494ec2a80669e950569
      if (nif.length != 9) return false;
      if(!['1', '2', '3', '5', '6', '8'].includes(nif.substr(0,1)) && 
        !['45', '70', '71', '72', '77', '79', '90', '91', '98', '99'].includes(nif.substr(0,2)))
          return false;

      let total = nif[0] * 9 + nif[1] * 8 + nif[2] * 7 + nif[3] * 6 + nif[4] * 5 + nif[5] * 4 + nif[6] * 3 + nif[7] * 2;

      let modulo11 = total - parseInt(total / 11) * 11;
      let comparador = modulo11 == 1 || modulo11 == 0 ? 0 : 11 - modulo11;

      return nif[8] == comparador;
    }
  },
  computed: {
    valid() {
      if (this.answer === undefined)
        return !this.question.required;
      return this.validateNIF(this.answer.toString());
    }
  }
};
</script>

<style scoped>
  input::-webkit-outer-spin-button,
  input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }

  /* Firefox */
  input[type=number] {
    -moz-appearance: textfield;
  }
</style>